<template>
    <button class="wl-btns" :class="[type,{'is-disabled':disabledBtns}]" :disabled="disabledBtns"
            @click="handleClick"
    >
        <slot></slot>
    </button>
</template>
<script>
    export default {
        name: 'btns',
        props:{
            type: {
                type: String,
                default: 'default'
            },
            disabled: {
                type: String,
                default: ''
            },
        },
        computed:{
            disabledBtns(){
                let flag = false;
                if(this.disabled == 'true'){
                    flag = true;
                }else if(this.disabled == 'false' || this.disabled == ''){
                    flag = false;
                }
                return flag;
            }
        },
        data() {
            return {

            }
        },
        mounted(){
            //console.log(this.disabled);
        },
        methods:{
            handleClick(evt) {
                this.$emit('click', evt);
            }
        }
    }
</script>
<style scoped>

</style>


