<template>
    <div class="wl-button">
        <div class="h-headtitle">Button 按钮</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                按钮用于触发一个操作，如提交表单。
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">
                <Btn type="primary" disabled="false" >主要按钮</Btn>
                <Btn type="default">默认按钮</Btn>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                &lt;wl-button type="primary" disabled="false" &gt;主要按钮&lt;/wl-button&gt;
                &lt;wl-button type="default" &gt;默认按钮&lt;/wl-button&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>

        </div>

        <div class="h-blocks">
            <div class="h-title">禁用状态</div>
            <div class="hb-a">
                <Btn type="primary" disabled="true" >主要按钮</Btn>
                <Btn type="default" disabled="true" >默认按钮</Btn>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                &lt;wl-button type="primary" disabled="false" &gt;主要按钮&lt;/wl-button&gt;
                &lt;wl-button type="default" &gt;默认按钮&lt;/wl-button&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>

        <div class="h-blocks">
            <div class="h-title">文字按钮</div>
            <div class="hb-a">
                <Btn type="text" >文字按钮</Btn>
                <Btn type="text" disabled="true" >文字按钮</Btn>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                &lt;wl-button type="text" &gt;文字按钮&lt;/wl-button&gt;
                &lt;wl-button type="text"  disabled="true" &gt;文字按钮&lt;/wl-button&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>

    </div>
</template>

<script>
    import Btn from '@/components/template/Button'
    export default {
        name: 'wlbutton',
        data() {
            return {

            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{
            Btn
        }
    }
</script>
<style scoped>

</style>



